// OCR()
import React, { useState, useEffect } from 'react';
import Tesseract from 'tesseract.js';
import db from '../firebase';
import { collection, addDoc } from 'firebase/firestore';

function OCR({ image, clipPath, onRestart, onExit }) {
  const [text, setText] = useState('');

  // 
  const handleSave = async () => {
    await addDoc(collection(db, 'collection'), { field1: image, field2: text });
    alert('');
  };

  useEffect(() => {

    // OCR
    const doOCR = async () => {
      const { data } = await Tesseract.recognize(image, 'jpn', { logger: (m) => console.log(m) });
      setText(data.text);
    };
    doOCR();
  }, [image]);

  return (
    <div>
      <h2></h2>
      <img src={image} alt="" style={{ clipPath: clipPath, width: '25%' }} /><br />
      <button onClick={onRestart}></button>
      <button onClick={onExit}></button>
      <button onClick={handleSave}></button>
      <h2></h2>
      <p>{text}</p>
    </div>
  );
}

export default OCR;
