import React from 'react';

function Home({ onCamera, onAlbum }) {
  return (
    <div>
      <h2></h2>
      <img src="./images/book.gif" alt="" style={{ width: '25%' }} /><br/>
      <p></p>
      <button onClick={onCamera}></button>
      <button onClick={onAlbum}></button>
    </div>
  );
}

export default Home;
